.home {
    color: black;
}

.home {
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.headerContainer {
    width: auto;
    margin-left: 50px;
    
}

.headerContainer h1 {
    justify-content: center;
    align-items: center;
    font-size: 90px;
    height: 60px;
    font-weight: 50;
    color: black;
    padding-bottom: 50px;
}

.headerContainer p {
    padding: 0;
    font-size: 40px;
    font-weight: lighter;
    color: black;
}
.button-home{
    text-decoration: none;
}




@media only screen and (max-width: 650px) {
    .home {
        justify-content: center;
        align-items: center;
    }


    .headerContainer {
        margin-left: 0px;
        border-radius: 10px;
        padding: 10px;
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: #121619;
        color: white;
    }

    .headerContainer h1 {
        font-size: 40px;
        height: 30px;
        color: white;
    }

    .headerContainer p {
        font-size: 30px;
        color: white;
    }

  
}